import packageJson from '../../package.json';

//  package.json 
export const CURRENT_VERSION = packageJson.version;

// GitHub 
const GITHUB_REPO = 'mzrodyu/maomaomz';
const GITHUB_API_BASE = 'https://api.github.com';

// LocalStorage 
const LAST_CHECK_KEY = 'maomaomz_last_version_check';
const IGNORED_VERSION_KEY = 'maomaomz_ignored_version';

/**
 * 
 * : 1  v1 > v2, -1  v1 < v2, 0 
 */
function compareVersions(v1: string, v2: string): number {
  const parts1 = v1.replace(/^v/, '').split('.').map(Number);
  const parts2 = v2.replace(/^v/, '').split('.').map(Number);

  for (let i = 0; i < Math.max(parts1.length, parts2.length); i++) {
    const num1 = parts1[i] || 0;
    const num2 = parts2[i] || 0;

    if (num1 > num2) return 1;
    if (num1 < num2) return -1;
  }

  return 0;
}

/**
 *  GitHub 
 */
async function fetchLatestVersion(): Promise<{ version: string; url: string; notes: string } | null> {
  try {
    console.log('  GitHub API ...');

    //  GitHub Releases API 
    const response = await fetch(`${GITHUB_API_BASE}/repos/${GITHUB_REPO}/releases/latest`, {
      headers: {
        Accept: 'application/vnd.github.v3+json',
      },
      // 
      signal: AbortSignal.timeout(10000), // 10
    });

    console.log(` GitHub API : ${response.status} ${response.statusText}`);

    if (!response.ok) {
      console.warn(` GitHub API  (${response.status}) jsDelivr CDN...`);
      //  jsDelivr CDN  package.json
      return await fetchVersionFromCDN();
    }

    const data = await response.json();
    console.log(' :', data.tag_name);

    return {
      version: data.tag_name.replace(/^v/, ''),
      url: data.html_url,
      notes: data.body || '',
    };
  } catch (error: any) {
    console.error(' GitHub API :', error.message || error);

    // 
    console.warn('  jsDelivr CDN ...');
    return await fetchVersionFromCDN();
  }
}

/**
 *  jsDelivr CDN  package.json
 */
async function fetchVersionFromCDN(): Promise<{ version: string; url: string; notes: string } | null> {
  try {
    console.log('  jsDelivr CDN ...');

    //  jsDelivr CDN
    const response = await fetch(`https://cdn.jsdelivr.net/gh/${GITHUB_REPO}@main/package.json`, {
      cache: 'no-store',
      signal: AbortSignal.timeout(10000),
    });

    if (!response.ok) {
      console.warn(` jsDelivr CDN  (${response.status})`);
      return null;
    }

    const data = await response.json();
    console.log('  CDN :', data.version);

    return {
      version: data.version,
      url: `https://github.com/${GITHUB_REPO}/releases/latest`,
      notes: `: ${data.version}\n\n GitHub `,
    };
  } catch (error: any) {
    console.error(' jsDelivr CDN :', error.message || error);
    return null;
  }
}

/**
 * 
 * @param force 
 */
export async function checkForUpdates(force: boolean = false): Promise<{
  hasUpdate: boolean;
  latestVersion?: string;
  currentVersion: string;
  updateUrl?: string;
  notes?: string;
} | null> {
  try {
    // 
    if (!force) {
      const lastCheck = localStorage.getItem(LAST_CHECK_KEY);
      if (lastCheck) {
        const lastCheckTime = parseInt(lastCheck);
        const now = Date.now();
        const oneDay = 24 * 60 * 60 * 1000;

        if (now - lastCheckTime < oneDay) {
          console.log(' 24');
          return null;
        }
      }
    }

    console.log(' ...');

    const latest = await fetchLatestVersion();

    if (!latest) {
      console.warn(' ');
      return null;
    }

    // 
    localStorage.setItem(LAST_CHECK_KEY, Date.now().toString());

    // 
    const hasUpdate = compareVersions(latest.version, CURRENT_VERSION) > 0;

    // 
    if (!force) {
      const ignoredVersion = localStorage.getItem(IGNORED_VERSION_KEY);
      if (ignoredVersion === latest.version) {
        console.log(`  ${latest.version} `);
        return null;
      }
    }

    return {
      hasUpdate,
      latestVersion: latest.version,
      currentVersion: CURRENT_VERSION,
      updateUrl: latest.url,
      notes: latest.notes,
    };
  } catch (error) {
    console.error(' :', error);
    return null;
  }
}

