/**
 * 
 */
export async function autoCheckUpdates(): Promise<void> {
  const result = await checkForUpdates(false);

  if (result && result.hasUpdate && result.latestVersion && result.updateUrl && result.notes) {
    console.log(` : ${result.latestVersion} (: ${result.currentVersion})`);
    showUpdateDialog({
      latestVersion: result.latestVersion,
      currentVersion: result.currentVersion,
      updateUrl: result.updateUrl,
      notes: result.notes,
    });
  }
}

/**
 * 
 */
export async function manualCheckUpdates(): Promise<void> {
  console.log(' ...');
  (window as any).toastr?.info('...', '', { timeOut: 3000 });

  const result = await checkForUpdates(true);

  if (!result) {
    console.error(' ');
    (window as any).toastr?.error(
      ' \n\n\n1. GitHub API \n2. \n3. CDN \n\n',
      '',
      { timeOut: 8000 },
    );
    return;
  }

  if (result.hasUpdate && result.latestVersion && result.updateUrl && result.notes) {
    console.log(` : ${result.latestVersion} (: ${result.currentVersion})`);
    showUpdateDialog({
      latestVersion: result.latestVersion,
      currentVersion: result.currentVersion,
      updateUrl: result.updateUrl,
      notes: result.notes,
    });
  } else {
    console.log(` : ${result.currentVersion}`);
    (window as any).toastr?.success(`  v${result.currentVersion}`, '');
  }
}
