// Cloudflare Workers 
const AUTH_API_URL = 'https://maomaomz-auth.baobaoyu999727272.workers.dev';

// LocalStorage 
const STORAGE_KEY = 'maomaomz_auth_code';
const STORAGE_VERIFIED_KEY = 'maomaomz_auth_verified';

/**
 *  API 
 */
function getCurrentApiEndpoint(): string {
  try {
    //  SillyTavern  API 
    let apiUrl = (window as any).api_server || '';
    const apiType = (window as any).main_api || 'unknown';

    //  apiUrl 
    if (typeof apiUrl === 'object') {
      console.warn('API:', apiUrl);
      apiUrl = '';
    }

    // 
    apiUrl = String(apiUrl || '').trim();

    if (apiUrl && apiUrl !== '' && apiUrl !== '[object Object]') {
      // URL
      try {
        const url = new URL(apiUrl);
        return url.hostname || apiUrl;
      } catch {
        // URL
        return apiUrl;
      }
    }

    // URLAPI
    return String(apiType || 'unknown');
  } catch (error) {
    console.error('API:', error);
    return 'unknown';
  }
}

/**
 * API
 */
async function verifyAuthCode(code: string): Promise<{ valid: boolean; message: string }> {
  try {
    //  API 
    const apiEndpoint = getCurrentApiEndpoint();

    console.log(' ...');
    console.log(' API:', apiEndpoint);

    const response = await fetch(`${AUTH_API_URL}/verify`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        code: code.trim().toUpperCase(),
        apiEndpoint: apiEndpoint, //   API 
        timestamp: new Date().toISOString(),
      }),
    });

    if (!response.ok) {
      return {
        valid: false,
        message: ' ',
      };
    }

    const data = await response.json();
    return data;
  } catch (error) {
    console.error(':', error);
    return {
      valid: false,
      message: ' : ' + (error as Error).message,
    };
  }
}

/**
 * 
 */
function showAuthDialog(): Promise<string | null> {
  return new Promise(resolve => {
    // 
    const overlay = document.createElement('div');
    overlay.id = 'maomaomz-auth-overlay';
    overlay.style.cssText = `
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgba(0, 0, 0, 0.92);
      z-index: 9999999 !important;
      display: flex;
      align-items: center;
      justify-content: center;
      backdrop-filter: blur(10px);
      animation: fadeIn 0.3s ease;
    `;

    // 
    const dialog = document.createElement('div');
    dialog.style.cssText = `
      background: linear-gradient(135deg, #1a1a1a 0%, #2a1a1a 50%, #1a2a2a 100%);
      border: 2px solid #3a3a3a;
      border-radius: 20px;
      padding: 40px;
      max-width: 500px;
      width: 90%;
      box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
      color: #e0e0e0;
      font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
    `;

    dialog.innerHTML = `
      <style>
        @keyframes fadeIn {
          from { opacity: 0; }
          to { opacity: 1; }
        }
        @keyframes slideUp {
          from { transform: translateY(20px); opacity: 0; }
          to { transform: translateY(0); opacity: 1; }
        }
      </style>
      <div style="text-align: center; animation: slideUp 0.4s ease;">
        <div style="font-size: 60px; margin-bottom: 20px;"></div>
        <h2 style="
          margin: 0 0 15px 0;
          font-size: 28px;
          background: linear-gradient(135deg, #ff9500 0%, #ffa500 100%);
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
          background-clip: text;
        ">
          
        </h2>
        <div style="
          background: linear-gradient(135deg, #dc2626 0%, #991b1b 100%);
          color: #fff;
          padding: 12px 20px;
          border-radius: 10px;
          margin: 20px 0;
          font-weight: 700;
          font-size: 14px;
          letter-spacing: 1px;
          box-shadow: 0 4px 16px rgba(220, 38, 38, 0.3);
        ">
            
        </div>
        <p style="
          margin: 20px 0;
          color: #ccc;
          line-height: 1.6;
          font-size: 15px;
        ">
          <br>
          <span style="font-size: 13px; color: #888;">
             Discord 
          </span>
        </p>
        <input 
          type="text" 
          id="authCodeInput" 
          placeholder="MEOW-20251111-ABCD"
          style="
            width: 100%;
            padding: 14px 16px;
            background: #0a0a0a;
            border: 2px solid #3a3a3a;
            border-radius: 12px;
            color: #fff;
            font-size: 16px;
            font-family: 'Courier New', monospace;
            letter-spacing: 1px;
            text-align: center;
            text-transform: uppercase;
            transition: border-color 0.3s ease;
            margin-bottom: 20px;
            box-sizing: border-box;
          "
        />
        <div style="display: flex; gap: 12px;">
          <button 
            id="authSubmitBtn"
            style="
              flex: 1;
              padding: 14px 24px;
              background: linear-gradient(135deg, #4a9eff 0%, #3b82f6 100%);
              border: none;
              border-radius: 12px;
              color: #fff;
              font-size: 16px;
              font-weight: 600;
              cursor: pointer;
              transition: all 0.3s ease;
              box-shadow: 0 4px 16px rgba(74, 158, 255, 0.3);
            "
          >
             
          </button>
          <button 
            id="authCancelBtn"
            style="
              padding: 14px 24px;
              background: linear-gradient(135deg, #6b7280 0%, #4b5563 100%);
              border: none;
              border-radius: 12px;
              color: #fff;
              font-size: 16px;
              font-weight: 600;
              cursor: pointer;
              transition: all 0.3s ease;
              box-shadow: 0 4px 16px rgba(107, 114, 128, 0.3);
            "
          >
             
          </button>
        </div>
        <p style="
          margin-top: 20px;
          font-size: 12px;
          color: #666;
          line-height: 1.5;
        ">
          <br>
           Discord 
        </p>
      </div>
    `;

    overlay.appendChild(dialog);
    document.body.appendChild(overlay);

    const input = dialog.querySelector('#authCodeInput') as HTMLInputElement;
    const submitBtn = dialog.querySelector('#authSubmitBtn') as HTMLButtonElement;
    const cancelBtn = dialog.querySelector('#authCancelBtn') as HTMLButtonElement;

    // 
    setTimeout(() => input.focus(), 100);

    // 
    submitBtn.addEventListener('mouseenter', () => {
      submitBtn.style.transform = 'translateY(-2px)';
      submitBtn.style.boxShadow = '0 6px 20px rgba(74, 158, 255, 0.5)';
    });
    submitBtn.addEventListener('mouseleave', () => {
      submitBtn.style.transform = 'translateY(0)';
      submitBtn.style.boxShadow = '0 4px 16px rgba(74, 158, 255, 0.3)';
    });

    cancelBtn.addEventListener('mouseenter', () => {
      cancelBtn.style.transform = 'translateY(-2px)';
      cancelBtn.style.boxShadow = '0 6px 20px rgba(107, 114, 128, 0.5)';
    });
    cancelBtn.addEventListener('mouseleave', () => {
      cancelBtn.style.transform = 'translateY(0)';
      cancelBtn.style.boxShadow = '0 4px 16px rgba(107, 114, 128, 0.3)';
    });

    // 
    input.addEventListener('focus', () => {
      input.style.borderColor = '#4a9eff';
      input.style.boxShadow = '0 0 0 3px rgba(74, 158, 255, 0.1)';
    });
    input.addEventListener('blur', () => {
      input.style.borderColor = '#3a3a3a';
      input.style.boxShadow = 'none';
    });

    // 
    const handleSubmit = () => {
      const code = input.value.trim();
      if (!code) {
        input.style.borderColor = '#ef4444';
        input.focus();
        return;
      }
      document.body.removeChild(overlay);
      resolve(code);
    };

    submitBtn.addEventListener('click', handleSubmit);
    input.addEventListener('keypress', e => {
      if (e.key === 'Enter') {
        handleSubmit();
      }
    });

    // 
    cancelBtn.addEventListener('click', () => {
      document.body.removeChild(overlay);
      resolve(null);
    });
  });
}

/**
 * 
 */
export async function checkAuthorization(): Promise<boolean> {
  console.log(' ...');

  // 
  const oldOverlay = document.getElementById('maomaomz-auth-overlay');
  if (oldOverlay) {
    oldOverlay.remove();
  }

  // 
  const savedCode = localStorage.getItem(STORAGE_KEY);

  // 
  if (savedCode) {
    console.log(' ...');
    const result = await verifyAuthCode(savedCode);

    if (result.valid) {
      console.log(' ');
      localStorage.setItem(STORAGE_VERIFIED_KEY, 'true');

      // 
      setTimeout(() => {
        (window as any).toastr?.success(' ', '', {
          timeOut: 2000,
        });
      }, 300);

      return true;
    } else {
      console.warn(' ');
      localStorage.removeItem(STORAGE_KEY);
      localStorage.removeItem(STORAGE_VERIFIED_KEY);
    }
  }

  //  - 
  console.log(' ...');

  let attempts = 0;
  const MAX_ATTEMPTS = 5; // 

  while (attempts < MAX_ATTEMPTS) {
    //  
    const code = await showAuthDialog();

    if (!code) {
      //  - 
      console.error(' ');

      const confirmCancel = confirm(
        ' \n\n\n\n""\n""',
      );

      if (confirmCancel) {
        (window as any).toastr?.error(' ', '', {
          timeOut: 0,
          extendedTimeOut: 0,
        });
        return false;
      } else {
        // 
        continue;
      }
    }

    attempts++;
    console.log(` ... ( ${attempts}/${MAX_ATTEMPTS})`);

    // 
    (window as any).toastr?.info(' ...', '', { timeOut: 3000 });

    const result = await verifyAuthCode(code);

    if (result.valid) {
      // 
      localStorage.setItem(STORAGE_KEY, code);
      localStorage.setItem(STORAGE_VERIFIED_KEY, 'true');
      console.log(' ');
      (window as any).toastr?.success(result.message, '', {
        timeOut: 3000,
      });
      return true;
    } else {
      console.warn(`  ( ${attempts}/${MAX_ATTEMPTS}):`, result.message);
      (window as any).toastr?.error(result.message, ` (${attempts}/${MAX_ATTEMPTS})`, {
        timeOut: 5000,
      });

      if (attempts >= MAX_ATTEMPTS) {
        (window as any).toastr?.error(' \n\n', '', {
          timeOut: 0,
          extendedTimeOut: 0,
        });
        return false;
      }
    }
  }

  return false;
}

/**
 * 
 */
export function isAuthorized(): boolean {
  const verified = localStorage.getItem(STORAGE_VERIFIED_KEY);
  return verified === 'true';
}

/**
 * 
 */
export function clearAuthorization(): void {
  localStorage.removeItem(STORAGE_KEY);
  localStorage.removeItem(STORAGE_VERIFIED_KEY);
  console.log(' ');
  (window as any).toastr?.info('');

  // 
  document.getElementById('maomaomz-auth-overlay')?.remove();
  document.getElementById('maomaomz-auth-dialog')?.remove();
}

// 
export const clearAuth = clearAuthorization;
